import React from "react"
import Layout from "../components/layout/Layout";
import { ProjectBox } from '../components/projectboxes/ProjectBox';
// import { GatsbyImage } from "gatsby-plugin-image"
import project1 from '../images/riverfish.jpg';
import project2 from '../images/mlr.jpg';
import project3 from '../images/flighttest2.jpg';
import superMariota from '../images/supermariotamainscreen.png';

import { Grid } from "@material-ui/core"
import { Link } from "gatsby";
// import { ProjectBox } from "../components/projectbox"
// import { graphql } from 'gatsby'

export default function ProjectsPage() {
	return(
		<Layout>
			<div style={{padding: 15, color: 'white'}}>
				<div style={{opacity: 0.666}}><i>"...creation is embarrassing. For every new good idea you have, there are a hundred, ten thousand foolish ones, which you naturally do not care to display."</i></div>
				<div style={{textAlign: 'right', opacity: 0.8}}><a href="https://www.technologyreview.com/2014/10/20/169899/isaac-asimov-asks-how-do-people-get-new-ideas/" style={{color: 'lightgrey'}}>- Isaac Asimov</a></div>
			</div>
			<div style={{marginBottom: 40, color: 'white'}}>
				<div style={{fontSize: '16pt', fontWeight: 500, marginTop: 10, marginBottom: 10, color: 'white'}}>Featured Projects</div>
				<Grid container spacing={2}>
					<Grid item xs={4}>
						<Link to="/projects/AquaticLibrary/" target="_blank" style={{textDecoration: 'none'}}>
							<ProjectBox height={200} imageSrc={project1} boxText="The Open Source Aquatic Library" />
						</Link>
					</Grid>
					<Grid item xs={4}>
						<Link to="/projects/RugbyApp/" target="_blank" style={{textDecoration: 'none'}}>
							<ProjectBox height={200} imageSrc={project2} boxText="Major League Rugby App" />
						</Link>
					</Grid>
					<Grid item xs={4}>
						<Link to="/projects/SeniorDesign/" target="_blank" style={{textDecoration: 'none'}}>
							<ProjectBox height={200} imageSrc={project3} boxText="Senior Design" />
						</Link>
					</Grid>
				</Grid>
			</div>
			
			<div style={{marginBottom: 40, color: 'white'}}>
				<div style={{fontSize: '16pt', fontWeight: 500, marginTop: 10, marginBottom: 10, color: 'white'}}>All Projects</div>
				<Grid container spacing={2}>
					<Grid item xs={4}>
						<Link to="/projects/SuperMariotaStadium/" target="_blank" style={{textDecoration: 'none'}}>
							<ProjectBox height={200} imageSrc={superMariota} boxText="Super Mariota Stadium" />
						</Link>
					</Grid>
				</Grid>
			</div>
		</Layout>
	)
}